import React, { useState } from 'react';
import {ReactComponent as Remove} from '../../assets/icons/delete-trash.svg'
import {ReactComponent as Plus} from '../../assets/icons/plus.svg'
import {ReactComponent as Minus} from '../../assets/icons/minus.svg'
import GreyPhone from "../../assets/images/12454-787bigeek_image1.jpeg";
import './cartproduct.css';

const CartProduct = ({ totalCount, setTotalCount }) => {

  const [ count, setCount ] = useState(1);

  const calcCount = (action) => {
    if (action === 'minus' && count !== 1) {
      setCount(count - 1);
      setTotalCount(totalCount - 1);
    } else if (action === 'plus') {
      setCount(count + 1);
      setTotalCount(totalCount + 1);
    }
  };

  const stepperAction = (action) => {
    return (
      <button className='product-item__button' onClick={() => calcCount(action)}>
          {
            action === 'minus'
              ? <Minus className='product-item__icon'/>
              : <Plus className='product-item__icon'/>
          }
      </button>
    )
  };

  return (
    <li className='product-item'>
      <div className='product-item__img'>
        <img src={GreyPhone} />
      </div>
      <div className='product-item__block'>
        <p className='product-item__price'>
          78 990  <span>x {count}</span>
        </p>
        <p className='product-item__description'>
          Apple iPhone 14 Pro Max 128GB (- | Deep Purple)
        </p>
        <div className='product-item__actions'>
          <div className='product-item__stepper'>
            {stepperAction('minus')}
            <div className='product-item__count'>{count}</div>
            {stepperAction('plus')}
          </div>
          <div className='product-item__remove'>
            <Remove className='product-item__icon product-item__icon--remove' />
          </div>
        </div>
      </div>
  </li>
  )
}

export default CartProduct;