const tg = window.Telegram.WebApp;

export function useTelegram() {

    const onClose = () => {
        tg.close()
    };

    const onToggleButton = () => {
        if(tg.MainButton.isVisible) {
            tg.MainButton.hide();
        } else {
            tg.MainButton.show();
        }
    };

    const backOnClick = (callBack) => tg.BackButton.onClick(callBack);
    const backOffClick = (callBack) => tg.BackButton.offClick(callBack);


    return {
        onClose,
        onToggleButton,
        backOnClick,
        backOffClick,
        tg,
        user: tg.initDataUnsafe?.user,
        queryId: tg.initDataUnsafe?.query_id,
    }
};